.centered{
  visibility: hidden;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 5px;
  height: 5px;
  border-radius: 50%;
  background: red 
}

.topLeft {
  visibility: hidden;
  background: red;
  height: 5px;
  width: 5px;
  border-radius: 50%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
}

.topRight {
  visibility: hidden;
  background: red;
  height: 5px;
  width: 5px;
  border-radius: 50%;
  position: absolute;
  top: 0;
  right: 0;
  z-index: 1;
}

.bottomLeft {
